.overlay
    left:0%
    top:0%
    position: fixed
    display: flex
    justify-content: center
    align-items: center
    background-color: rgba(65, 49, 66, 0.5)
    height: 100%
    width: 100%
    z-index: 500

.form
    position: fixed
    padding: 0 0.5rem
    display: block
    border:  10px solid transparent
    border-radius: 15px
    z-index: 550

.form h1
    margin-top: 10px
    border-bottom: 1px solid black

.form h3
    margin-bottom: 7px

.form select
    outline: none
    border: none
    background: transparent
    border-bottom: 1px solid black

.formally
    left: 40% 
    align-content: center

.formname
    display: flex

.input
    text-align: left

.input input
    border: none
    background: transparent
    border-bottom: 1px solid black
    user-select: none
    outline: none

.input:nth-last-of-type(1)
    margin-left: 40px

.FormButtons
    display: flex
    justify-content: center


.gotodungeon
    margin-top: 15px
    margin: auto
    width: 200px
    height: 60px
    border: 3px solid
    border-radius: 8px
    background-color: black
    color: rgb(153, 0, 255)
    font-size: 20px
    font-weight: 650
    cursor: pointer

.gotodungeon:hover
    background-color: white
    opacity: 0.9

.gotodungeon:active
    background-color: silver

.OhSHitIamSorry
    padding: 1rem 2rem
    border: 3px solid
    text-align: center
    border-radius: 8px
    background-color: black
    color: rgb(153, 0, 255)
    font-size: 20px
    font-weight: 650
    cursor: pointer

.OhSHitIamSorry:hover
    background-color: white
    opacity: 0.9

.OhSHitIamSorry:active
    background-color: silver

.form-header
    user-select: none
    background-color: white
    width: 100%
    font-size: 3rem
    border-bottom: 1px solid black
    margin-bottom: 0.5rem
    max-height: 3.5rem
    overflow: hidden

.form-body
    height:90%

.form textarea
    width: 380px
    border: none
    background: transparent
    border-bottom: 1px solid black
    user-select: none
    outline: none
    margin-bottom: 10px

@media screen and (max-width:768px) 
    .form 
        width: 350px
    .form input
        width: 140px
    .form textarea
        width: 330px
    .form button
        width: 150px
        font-size: 16px


